exports.push([module.id, "\n.action {\n  color: #e20000;\n  font-size: 16px;\n}\n.notAction {\n  color: #999;\n  font-size: 16px;\n}\n", "", {"version":3,"sources":["/www/wwwroot/piaobo/packages/thinksns-plus-authorization-pb-h5/src/views/FeedBack.vue?2f29ea2e"],"names":[],"mappings":";AAwFA;EACA,eAAA;EACA,gBAAA;CACA;AACA;EACA,YAAA;EACA,gBAAA;CACA","file":"FeedBack.vue","sourcesContent":["<template>\n<transition name=\"custom-classes-transition\" enter-active-class=\"animated slideInRight\" leave-active-class=\"animated slideOutRight\">\n    <div class=\"feedBack\" :class=\"$style.postRoot\">\n        <header class=\"commonHeader\">\n            <Row :gutter=\"24\">\n                <Col span=\"5\" style=\"display: flex; justify-content: flex-start\" @click.native=\"goTo(-1)\">\n                <BackIcon height=\"21\" width=\"21\" color=\"#999\" />\n                </Col>\n                <Col span=\"14\" class=\"title-col\">意见反馈</Col>\n                <Col span=\"5\" class=\"header-end-col\">\n                    <LoadingWhiteIcon height=\"21\" width=\"21\" v-if=\"loading\" />\n                    <span :class=\"{ action: !isDisabled, notAction: isDisabled}\"  v-else @click=\"postFeed\">提交</span>\n                </Col>\n            </Row>\n        </header>\n        <div :class=\"$style.content\">\n            <Row :gutter=\"24\">\n              <Col span=\"24\">\n                <Input \n                  v-model=\"content\" \n                  :autosize=\"{minRows: 6, maxRows: 12}\" \n                  :autofocus=\"true\" \n                  :maxlength=\"255\" \n                  type=\"textarea\" \n                  :class=\"$style.contentInput\" \n                  placeholder=\"请输入反馈，我们将为您不断改进\"\n                  v-childfocus\n                />\n              </Col>\n            </Row>\n        </div>\n    </div>\n</transition>\n</template>\n<script>\nimport BackIcon from '../icons/Back';\nimport LoadingWhiteIcon from '../icons/LoadingWhite';\nimport { goTo } from '../utils/changeUrl';\nimport { createAPI, addAccessToken } from '../utils/request';\nimport { NOTICE } from '../stores/types';\n\nconst feedBack = {\n    name: \"feedBack\",\n    components: {\n        BackIcon,\n        LoadingWhiteIcon\n    },\n    data: () => ({\n        loading: false,\n        content: ''\n    }),\n    methods:{\n        goTo,\n        postFeed(){\n            if(this.isDisabled) return false;\n            \n            this.loading = true;     \n            let system_mark = +(window.TS_WEB.currentUserId + (new Date).getTime());\n            addAccessToken().post(createAPI(\"user/feedback\"),{\n                content: this.content,\n                system_mark\n            },{\n                validateStatus: status => status === 201\n            })\n            .then(({data={message: '未知错误'}})=>{\n                this.$store.dispatch(NOTICE, cb => {\n                  cb({\n                    text: data.message[0],\n                    time: 1500,\n                    status: true\n                  });\n                });\n                this.loading = false;\n                this.content = '';\n            });\n        }\n    },\n    computed:{\n        isDisabled () {\n          return !(this.content.length);\n        }\n    }\n}\n\nexport default feedBack;\n</script>\n\n<style>\n    .action {\n      color: #e20000;\n      font-size: 16px;\n    }\n    .notAction {\n      color: #999;\n      font-size: 16px;\n    }\n</style>\n\n<style lang=\"less\" module>\n  .postRoot {\n    position: fixed;\n    top: 0;\n    left: 0;\n    right: 0;\n    bottom: 0;\n    margin: 0;\n    overflow: hidden;\n    background: #fff;\n    z-index: 10;\n    display: block;\n    height: 100%;\n    width: 100%;\n    .uploadList {\n      padding: 0 4vw;\n      display: flex;\n      flex-wrap: wrap;\n    }\n    .contentInput {\n      padding: 6px 0;\n      input, textarea {\n        border: none;\n        padding: 4px 8px;\n        transition: none;\n        font-size: 14px;\n        &:focus, &:hover{\n          border: none;\n          outline: 0;\n          box-shadow: none;\n        }\n      }\n    }\n    .actionBtn {\n      font-size: 16px;\n      display: flex;\n      justify-content: flex-start;\n      padding: 6px 0;\n    }\n    .sendAction {\n      display: flex;\n      justify-content: flex-end;\n    }\n    .camera {\n      width: 22vw;\n      height: 22vw; \n      line-height: 22vw;\n      display: flex;\n      justify-content: center;\n      align-items: center;\n    }\n  }\n</style>"],"sourceRoot":""}]);

